.container {
    margin: 0 auto;
}

.title {
    margin-top: 20px;
    text-align: center;
}

.instructions {
    text-align: center;
    margin-bottom: 20px;
}

.signup {
    margin: 0 auto;
    border-color: #87a175;
    border-radius: 20px;
    border-style: solid;
    padding: 2% 15% 2% 15%;
}

label, input {
    display: inline-block;
    vertical-align: baseline;
    width: 100%;
}
button {
    margin-top: 20px;
}
label {
    color: #2D2D2D;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: .1rem;
}
input {
    margin-bottom: 10px;
}

form, input {
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}


