body {
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(44, 207, 44, 0.8)),
    to(rgba(235, 221, 25, 0.8))
  );
  background-image: -webkit-linear-gradient(
    rgba(44, 207, 44, 0.8),
    rgba(235, 221, 25, 0.8)
  );
  background-image: -o-linear-gradient(
    rgba(44, 207, 44, 0.8),
    rgba(235, 221, 25, 0.8)
  );
  background-image: linear-gradient(
    rgba(44, 207, 44, 0.8),
    rgba(235, 221, 25, 0.8)
  );
  font-family: 'Exo 2', 'sans-serif';
  margin-bottom: 100px;
}
#totals {
  color: white;
}
h5 {
  color: black;
}
#update {
  background: white;
}
#update-text {
  color: rgb(17, 90, 86);
}

#rent {
  background-color: rgb(17, 90, 86);
  border: 2px solid black;
}
#util {
  background-color: rgb(75, 196, 210);
  border: 2px solid black;
}
#car {
  background-color: rgb(235, 221, 25);
  border: 2px solid black;
}
#food {
  background-color: rgb(44, 207, 44);
  border: 2px solid black;
}
#credit {
  background-color: rgb(235, 141, 19);
  border: 2px solid black;
}
#loan {
  background-color: rgb(30, 113, 173);
  border: 2px solid black;
}
#medical {
  background-color: rgb(152, 229, 46);
  border: 2px solid black;
}
#other {
  background-color: rgb(45, 102, 9);
  border: 2px solid black;
}



